@import "_reset";
@import "_variables";

/*--- GENERAL STYLES ---*/
img {
	max-width: 100%;
	width: auto;
}
.section {
	position: relative;
	background: #ffffff;
}
a {
	@extend.animate;
}
button {
	cursor: pointer;
	@extend.animate;
}
.container {
	width: 100%;
	max-width: 1460px;
	margin: auto;
	padding-left: 15px;
	padding-right: 15px;
	position: relative;
}
.animate {
	-webkit-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
	transition: all 0.3s ease;
}
.btn {
	text-transform: uppercase;
    height: 45px;
    line-height: 40px;
    width: 156px;
    text-align: center;
    border: 2px solid #ffffff;
    -webkit-border-radius: 3px;
    border-radius: 3px;
    display: inline-block;
    color: #ffffff;
    font-weight: bold;
    background: none;
    cursor: pointer;
    position: relative;
    z-index: 1;
    @extend.animate;
    &:hover {
    	/* background: #ffffff; */
    	color: $blue;
    }
	&:after {
	  background: #ffffff;
	  content: '';
	  position: absolute;
	  z-index: -1;
	  @extend.animate;
	}
	&:after {
		height: 0;
		left: 50%;
		top: 50%;
		width: 0;
	}

	&:hover:after {
		height: 100%;
		left: 0;
		top: 0;
		width: 100%;
	}
}
.blue_btn {
	border-color: $blue;
	color: $blue;
	&:hover {
		border-color: $blue;
		color: $blue;
	}
}
.accent_btn {
	border-color: $accent;
	color: $accent;
	&:hover {
		border-color: $accent;
		color: $accent;
	}
}
.go_up_btn {
	background: $blue;
	height: 60px;
	width: 60px;
	-webkit-border-radius: 50%;
	border-radius: 50%;
	position: fixed;
	bottom: 100px;
	right: 50px;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    z-index: 1;
    -webkit-box-shadow: 0 0 8px 0px #fff;
    box-shadow: 0 0 8px 0px #fff;
    &:hover {
    	background: $accent;
    }
    i {
    	color: #ffffff;
    	font-size: 34px;
    }
}
.sandwich{
	display: none;
	position: relative;
	cursor: pointer;
	float: right;
	width: 70px;
    height: 70px;
    padding: 20px;
    z-index: 1;
	.piece {
		position: relative;
		width: 100%;
		height: 4px;
		background-color: #ffffff;
		-webkit-transition: all 0.3s ease;
		-o-transition: all 0.3s ease;
		transition: all 0.3s ease;
		&.top{
			top: 20%;
		}
		&.middle{
			top: 40%;
		}
		&.bottom{
			opacity: 1;
			top: 60%;
			-webkit-transition: all 0.3s ease;
			-o-transition: all 0.3s ease;
			transition: all 0.3s ease;
		}
	}
	&.active{
		.piece{
			&.top{
				top: 50%;
				-webkit-transform: rotate(135deg);
				-ms-transform: rotate(135deg);
				transform: rotate(135deg);
			}
			&.middle{
				top: 36%;
				-webkit-transform: rotate(-135deg);
				-ms-transform: rotate(-135deg);
				transform: rotate(-135deg);
			}
			&.bottom{
				top: 0;
				opacity: 0;
				-webkit-transform: rotate(90deg);
				-ms-transform: rotate(90deg);
				transform: rotate(90deg);
			}
		}
	}
}
.top_bg_image {
	height: 100vh;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	img {
		height: 100%;
		width: 100%;
		-o-object-fit: cover;
		object-fit: cover;
	}
	&:after {
		content: '';
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background: rgba(0, 0, 0, 0.3);
	}
}
.tags {
	text-align: center;
	a {
		color: $accent;
		text-transform: uppercase;
		&:after {
			content: ',';
		}
		&:last-child:after {
			content: none;
		}
		&:hover {
			color: $blue;
		}
	}
}
.article_info {
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-flex-wrap: wrap;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
	-webkit-box-pack: center;
	-webkit-justify-content: center;
	-ms-flex-pack: center;
	justify-content: center;
	-webkit-box-align: center;
	-webkit-align-items: center;
	-ms-flex-align: center;
	align-items: center;
	margin: auto;
	border-bottom: 1px solid $gray_light;
	margin-bottom: 15px;
	padding-top: 20px;
	padding-bottom: 20px;
	.article_decor_separator {
		position: relative;
		height: 20px;
		width: 20px;
		margin-left: 20px;
		margin-right: 20px;
		i {
			font-size: 8px;
			color: #666666;
			position: absolute;
			&:first-child {
				left: 0;
				top: -webkit-calc(50% - 4px);
				top: calc(50% - 4px);
			}
			&:nth-child(2) {
				top: 0;
				left: -webkit-calc(50% - 4px);
				left: calc(50% - 4px);
			}
			&:nth-child(3) {
				right: 0;
				top: -webkit-calc(50% - 4px);
				top: calc(50% - 4px);
			}
			&:last-child {
				bottom: 0;
				left: -webkit-calc(50% - 4px);
				left: calc(50% - 4px);
			}
		}
	}
	.arcticle_date,
	.article_author,
	.tags {
		text-transform: uppercase;
		/* width: -webkit-calc(100% / 3 - 40px);
		width: calc(100% / 3 - 40px); */
		text-align: center;
	}
	.article_author {
		color: $blue;
	}
}
.articles {
	.article_info {
		.arcticle_date,
		.article_author,
		.tags {
			width: -webkit-calc(100% / 3 - 40px);
			width: calc(100% / 3 - 40px);
		}
	}
}
