/*--- HEADER ---*/
.header {
	position: fixed;
	top: 0;
	height: 100px;
	width: 100%;
	z-index: 2;
	background: rgba(0, 0, 0, 0.7);
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	.container {
		display: -webkit-box;
		display: -webkit-flex;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-pack: justify;
		-webkit-justify-content: space-between;
		-ms-flex-pack: justify;
		justify-content: space-between;
		-webkit-box-align: center;
		-webkit-align-items: center;
		-ms-flex-align: center;
		align-items: center;
		height: 100%;
	}
}
.header_logo {
	height: 100%;
	padding-top: 10px;
	padding-bottom: 10px;
	img {
		max-width: 100%;
		max-height: 100%;
	}
}
.nav {
	li {
		cursor: pointer;
		-webkit-transition: all 0.3s ease;
		-o-transition: all 0.3s ease;
		transition: all 0.3s ease;
		position: relative;
		width: 130px;
		height: 50px;
		margin-left: 20px;
		display: inline-block;
		a {
			display: block;
			width: 100%;
			height: 100%;
			text-align: center;
			display: -webkit-box;
			display: -webkit-flex;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-align: center;
			-webkit-align-items: center;
			-ms-flex-align: center;
			align-items: center;
			-webkit-box-pack: center;
			-webkit-justify-content: center;
			-ms-flex-pack: center;
			justify-content: center;
			font-size: 22px;
			color: #ffffff;
		}
		&:before,
		&:after {
			position: absolute;
			content: "";
			right: 0;
			top: 0;
			background: $accent;
			-webkit-transition: all 0.3s ease;
			-o-transition: all 0.3s ease;
			transition: all 0.3s ease;
		}
		&:before {
			height: 0;
			width: 2px;
		}
		&:after {
			width: 0;
			height: 2px;
		}
		&:hover:before {
			height: 100%;
		}
		&:hover:after {
			width: 100%;
		}
		a:before,
		a:after {
			position: absolute;
			content: "";
			left: 0;
			bottom: 0;
			background: $accent;
			-webkit-transition: all 0.3s ease;
			-o-transition: all 0.3s ease;
			transition: all 0.3s ease;
		}
		a:before {
			width: 2px;
			height: 0;
		}
		a:after {
			width: 0;
			height: 2px;
		}

		a:hover:before {
			height: 100%;
		}
		a:hover:after {
			width: 100%;
		}
		&.active {
			&:before {
				height: 100%;
			}
			&:after {
				width: 100%;
			}
			a:before {
				height: 100%;
			}
			a:after {
				width: 100%;
			}
		}
	}
	&.active {
		display: block;
	}
}

