* {
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	font-family: 'PT Sans', Arial, sans-serif;
	font-size: 16px;
	margin: 0;
	padding: 0;
	outline: none;
	color: #1c1c1c;
	font-weight: 100;
}
a {
	color: inherit;
	text-decoration: none;
}
a:hover {
	color: inherit;
	text-decoration: none;
}
a:focus {
	color: inherit;
	text-decoration: none;
}
